<template>
    <div id="index-page">
        <!-- tab-bar部分 -->
        <tab-bar
        :currentIndex="currentIndex"
        :list="tabList"
        @indexChange="indexChange"
        ></tab-bar>
        <!-- tab-bar部分结束-->

        <!-- 主体部分 -->
        <div class="main-container">
            <keep-alive exclude="Problem,Answer">
                <router-view></router-view>
            </keep-alive>
        </div>
        <!-- 主体部分结束-->

    </div>
</template>

<script>
import TabBar from '../components/TabBar.vue'
export default {
  components: {
    TabBar
  },
  data () {
    return {
      currentIndex: 0,
      tabList: [
        {
          title: '训练教程',
          icon: 'el-icon-document',
          route: '/index/tutorial'
        },
        {
          title: '查看公告',
          icon: 'el-icon-alarm-clock',
          route: '/index/bulletin'
        },
        {
          title: '个人资料',
          icon: 'el-icon-user',
          route: '/index/userInfo'
        }]
    }
  },
  methods: {
    indexChange (e) {
      console.log(e)
      this.$router.push(this.tabList[e.index].route)
      this.currentIndex = e.index
    }
  }
}
</script>

<style scoped>
     #index-page
     {
         height: 100%;
         padding-top:60px;
     }
     .main-container
     {
         width: 90%;
         margin:0 auto;
         background-color: #fff;
         min-height: 100%;
     }
</style>
